<template>
  <chart-card :title="title">
    <template>
      <div style="height: 100%">
        <div :id="refName" :ref="refName" v-loading="loading" style="height: 230px;" />
      </div>
    </template>
  </chart-card>
</template>

<script>
import '@/plugins/echarts/echartsTheme.js'
import ChartCard from '@/views/analysis/ChartCard'
import { zoom } from '@/mixins/echarts'

export default {
  name: 'RatioCom',
  components: {
    ChartCard
  },
  mixins: [zoom],
  props: {
    title: {
      type: String,
      default: '合同到货分析'
    },
    chartLoading: {
      type: Boolean,
      default: false
    },
    chartData: {
      type: Object,
      default: () => {}
    }
  },
  data() {
    return {
      refName: 'Ratio',
      options: {},
      timer: null
    }
  },
  watch: {
    'chartData.xAxisData': function(value) {
      if (value.length) {
        this.resize()
        this.init()
        this.resize()
      }
    }
  },
  beforeDestroy() {
    this.timer && clearInterval(this.timer)
    this.chart && this.chart.dispose() && (this.chart = null)
  },
  mounted() {
    this.screenWidth = window.screen.width || document.documentElement.clientWidth
    this.screenHeight = window.screen.height || document.documentElement.clientHeight
    if (this.screenWidth && (this.screenWidth === 1024 || this.screenWidth < 1024)) {
      this.zoom = 0.8
    }
  },
  methods: {
    init() {
      const { data, xAxisData } = this.chartData
      const xAxis = [
        {
          data: xAxisData,
          // axisLabel: {
          //   interval: 0
          // },
          bottom: 200,
          axisTick: {
            length: 480 // 延长坐标轴刻度的长度，用作表格竖线
          },
          axisLabel: {
            show: true,
            interval: 0 // 使x轴文字显示全
            // formatter: (params) => {
            //   var newParamsName = ''
            //   var paramsNameNumber = params.length
            //   var provideNumber = 4 // 一行显示几个字
            //   var rowNumber = Math.ceil(paramsNameNumber / provideNumber)
            //   if (paramsNameNumber > provideNumber) {
            //     for (var p = 0; p < rowNumber; p++) {
            //       var tempStr = ''
            //       var start = p * provideNumber
            //       var end = start + provideNumber
            //       if (p === rowNumber - 1) {
            //         tempStr = params.substring(start, paramsNameNumber)
            //       } else {
            //         tempStr = params.substring(start, end) + '\n'
            //       }
            //       newParamsName += tempStr
            //     }
            //   } else {
            //     newParamsName = params
            //   }
            //   return newParamsName
            // }
          }
        }
      ]
      const legendData = []
      let bottom = 20
      const series = data.map((item, index) => {
        legendData.push(item.name)
        xAxis.push({
          data: item.data,
          position: 'bottom', // 将坐标轴定位到底部
          offset: 20 + index * 20, // 设置位置偏移
          axisTick: {
            show: false
          }
        })
        bottom += 20
        return {
          name: item.name,
          data: item.bar,
          stack: 'ad',
          type: 'bar',
          barWidth: 50
        }
      })
      // 最后加一个无data的项，作为表格最底部的线
      xAxis.push({
        position: 'bottom',
        offset: bottom,
        axisTick: {
          show: false
        }
      })
      this.getCompleteOptions(legendData, series, xAxis, bottom + 1)
    },
    getCompleteOptions(legendData, series, xAxis, bottom) {
      this.timer && clearInterval(this.timer)
      this.chart && this.chart.dispose() && (this.chart = null)
      this.chart = this.$echarts.init(this.$refs[this.refName], 'macarons')
      if (!this.chart) return
      if (!xAxis.length) return
      this.options = {
        grid: {
          bottom, // 调整图表容器距离底部的距离，给表格腾空间
          left: 120, // table 距离右侧的宽度
          top: 10,
          right: 10
        },
        legend: { // 图例调整位置，对应表格的相应行
          data: legendData,
          bottom: 0,
          left: 0,
          orient: 'vertical',
          itemGap: 5
        },
        xAxis,
        yAxis: {},
        series,
        dataZoom: [
          {
            type: 'inside', // 类型：内置型数据区域缩放组件
            xAxisIndex: [0, 1, 2, 3],
            startValue: 0,
            endValue: 3
          }
        ]
      }
      this.chart.setOption(this.options)
      this.chart.resize()
      this.timer = setInterval(() => {
        if (this.options.dataZoom[0].endValue === xAxis[0].data.length - 1) {
          this.options.dataZoom[0].startValue = 0
          this.options.dataZoom[0].endValue = 3
        } else {
          this.options.dataZoom[0].endValue = this.options.dataZoom[0].endValue + 1
          this.options.dataZoom[0].startValue = this.options.dataZoom[0].startValue + 1
        }
        this.chart.setOption(this.options)
        this.chart.resize()
      }, 2500)
    }
  }
}
</script>

<style lang='scss' scoped>

</style>
