<template>
  <el-row>
    <el-row :gutter="20" style="height: 100%;">
      <!-- 在途物资统计分析 -->
      <el-col style="height: 100%;">
        <chart-one
          ref="ChartOne"
          :key="viewKey"
          style="height: 100%;"
          v-bind="$attrs"
          :chart-loading="loading"
          :chart-data="percentageChart"
        />
      </el-col>
    </el-row>
  </el-row>

</template>

<script>
import ChartOne from './ChartOne'
import { getChartApi } from '@/apis/modules/analysis/orderContract'

export default {
  name: 'ChartsCom',
  components: {
    ChartOne
  },
  data() {
    return {
      viewKey: 1,
      loading: false,
      percentageChart: {}
    }
  },
  methods: {
    initCharts(params) {
      this.loading = true
      return new Promise((resolve, reject) => {
        getChartApi(params).then(res => {
          const { code, data } = res
          if (!code) {
            console.log(data)
            this.percentageChart = data
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    reset() {
      this.viewKey++
      this.$refs.ChartOne.resize()
    },
    handleUploadImg() {

    }
  }
}
</script>

