<template>
  <div class="app-container">
    <!-- 头部搜索 -->
    <div class="clearfix top-filter">
      <el-form ref="filterItem" :model="query" inline>
        <el-form-item prop="date" class="fl">
          <el-date-picker
            v-model="query.date"
            type="monthrange"
            clearable
            value-format="yyyy-MM"
            range-separator="至"
            start-placeholder="开始月份"
            end-placeholder="结束月份"
          />
        </el-form-item>
        <el-form-item prop="contractName" class="fl">
          <el-input v-model.trim="query.contractName" clearable placeholder="合同号" class="filter-item" style="width: 178px;" />
        </el-form-item>
        <el-form-item prop="supplierName" class="fl">
          <el-input v-model.trim="query.supplierName" clearable placeholder="供应商名称" class="filter-item" style="width: 178px;" />
        </el-form-item>
        <el-button
          class="filter-item"
          type="success"
          icon="el-icon-search"
          @click="searchInit"
        >搜索</el-button>
        <el-button
          class="filter-item"
          type="warning"
          icon="el-icon-refresh-left"
          @click="resetForm('filterItem')"
        >重置</el-button>
      </el-form>
      <!-- <el-button
        class="filter-item"
        type="primary"
        icon="el-icon-plus"
        @click="handleExport"
      >导出</el-button> -->
    </div>

    <el-row>
      <el-button
        class="filter-item"
        type="info"
        icon="el-icon-setting"
        @click="setTable"
      >设置</el-button>
      <el-button
        class="filter-item"
        type="info"
        icon="el-icon-setting"
        :loading="dialogLoading"
        @click="openDialog"
      >自定义统计</el-button>
      <!-- <el-button
        class="filter-item"
        type="primary"
        icon="el-icon-plus"
        @click="handleExport"
      >导出</el-button> -->
      <el-button
        class="filter-item"
        type="primary"
        icon="el-icon-plus"
        @click="handleExport1"
      >导出</el-button>
    </el-row>

    <!-- 图表折叠面板 -->
    <el-collapse v-model="collapseValue" accordion style="margin: 0 0 20px 0;" @change="collapseChange">
      <el-collapse-item title="图表展示" name="1">
        <chart-com ref="chartCom" :query="query" style="height: 310px;" />
      </el-collapse-item>
    </el-collapse>

    <!-- 列表 -->
    <el-row>
      <table-com
        :ref="refTableName"
        :key="baseTableKey"
        :ref-name="refName"
        :table-height-type="collapseValue ? 220 : 570"
        :loading="loading"
        :index="true"
        :selection="false"
        :table-column-sort="true"
        :form-ident="formIdent"
        :table-data="tableData"
        :column-list="columnList"
        :show-operate="true"
        :operation-width="120"
        :refresh-key="refreshKey"
        :expand-list="expandList"
        @rowClick="handleRowClick"
        @refresh="searchInit"
      >
        <template slot-scope="scope">
          <el-button type="warning" icon="el-icon-edit" @click="handleModify(scope.row)">详情</el-button>
        </template>
      </table-com>
    </el-row>
    <pagination :page.sync="pageInfo.current" :limit.sync="pageInfo.size" :total="total" :page-sizes="[50, 100, 150, 500]" @pagination="init" />

    <!-- 编辑弹窗 -->
    <edit-dialog
      v-model="dialogVisible"
      :form-ident="formIdent"
      :table-data.sync="dialogTableData"
      :column-list="columnListInput"
      :expand-list="expandList"
      @dialogConfirm="onDialogConfirm"
    />

  </div>
</template>

<script>
import tableHeadSet from '@/mixins/tableHeadSet'
import ChartCom from './Charts'
import paginationPage from '@/mixins/paginationPage'
import EditDialog from './Dialog'
import { getListApi, exportInventoryAnalysisApi, getChartApi } from '@/apis/modules/analysis/orderContract'
import { uploadApi } from '@/apis/modules/common'
import { promiseWrapper, requestWrapper } from '@/utils/utils'
import { Promise } from 'es6-promise'
import { generateMonthColumnSet } from './column'
import moment from 'moment'
moment.locale('zh-cn')
import { _debounce } from '@/utils'
import * as XLSX from 'xlsx'
import { saveAs } from 'file-saver'
const INIT_FORM = () => ({
  date: '',
  startDate: '',
  endDate: '',
  contractName: '',
  supplierName: ''
})

export default {
  // name: 'AnalysisOrderContract',
  components: {
    ChartCom,
    EditDialog
  },
  mixins: [tableHeadSet, paginationPage],
  data() {
    return {
      title: this.$route.meta.title,
      refTableName: 'baseTableRef',
      refName: 'table',
      loading: false,
      query: INIT_FORM(),
      tableData: [],
      columnList: [],
      columnSets: [
        {
          prop: 'supplierName',
          width: 280
        }
      ],
      columnSetsInput: [
      ],
      columnListInput: [],
      tableColumnSortData: [],
      dialogForm: {},
      editType: 'add',
      dialogVisible: false,
      dialogLoading: false,
      dialogTableData: [],
      exportLoading: false,
      monthMap: {},
      baseTableKey: 1,
      collapseValue: '1' // 折叠面板绑定数据
    }
  },
  computed: {
    dialogTitle() {
      if (this.editType === 'add') {
        return `新增${this.title}信息`
      } else {
        return `编辑${this.title}信息`
      }
    }
  },
  watch: {
    collapseValue: {
      handler() {
        this.$nextTick(() => {
          this.baseTableKey++
        })
      }
    },
    'query.date': {
      handler(arr) {
        if (!arr) {
          this.query.startDate = ''
          this.query.endDate = ''
          return
        }
        this.query.startDate = arr[0]
        this.query.endDate = arr[1]
      }
      // immediate: true
    }
  },
  mounted() {
    this.columnSets = [...this.columnSets]
    this.columnSetsInput = [...this.columnSets, ...generateMonthColumnSet(this.columnSets, 'input')]
    this.columnListInput = this.columnList.map(i => {
      return {
        ...i,
        module: 'input'
      }
    })
    this.initQueryTime()
    this.init()
  },
  methods: {
    initQueryTime() {
      const currentMonth = moment().format('yyyy-MM')
      console.log('当前月份:', currentMonth)
      const lastMonth = moment().subtract(1, 'months').format('yyyy-MM')
      console.log('上个月月份:', lastMonth)
      this.query.date = [lastMonth, currentMonth]
      this.query.startDate = lastMonth
      this.query.endDate = currentMonth
    },
    init() {
      this.loading = true
      const { date, ...query } = this.query
      console.log('date: ', date)
      const params = {
        ...query,
        page: {
          ...this.pageInfo
        }
      }
      return new Promise((resolve, reject) => {
        getListApi({
          ...params
        }).then(res => {
          const { code, data } = res
          if (!code) {
            this.baseTableKey++
            const { formIdent, formHeadSets, pageData } = data
            this.formIdent = formIdent
            this.columnList = this.initTableHeadMixins(formHeadSets)
            this.columnListInput = this.initTableHeadMixins(formHeadSets, 'input', 'columnSetsInput', 180)
            const { list, ...pageInfo } = pageData
            this.initPage(pageInfo)
            this.tableData = list
            if (this.collapseValue === '1') {
              this.$refs.chartCom.initCharts(params)
            }
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    handleRowClick({ row, column, cell, event }) {
      if (column.label === '操作') {
        return false
      }
    },
    /*
    * 用户修改完数据点击确定
    * 1. 提交所有数据给后台，后台生成echarts需要的数据
    * 2. 更新echarts
    */
    onDialogConfirm() {
      this.tableData = this.dialogTableData
      this.getChartData()
      this.dialogVisible = false
    },
    /*
    * 获取图表数据
    * 1. 根据最新更新的表格数据，获取图表所需数据
    * 2. 更新图表
    */
    async getChartData() {
      const res = await requestWrapper(getChartApi({
        dataList: this.handleSubmitCustomData()
      }))
      if (!res.success) return
      console.log('修改数据后重新生成的图表数据-----》', res.data)
      this.updateColumnChart('ChartOne', res.data)
    },
    // 更新图表
    updateColumnChart(name, { data, xAxisData }) {
      const chartCom = this.$refs.chartCom
      const com = chartCom.$refs[name]
      const xAxis = [
        {
          data: xAxisData,
          // axisLabel: {
          //   interval: 0
          // },
          bottom: 200,
          axisTick: {
            length: 480 // 延长坐标轴刻度的长度，用作表格竖线
          },
          axisLabel: {
            show: true,
            interval: 0, // 使x轴文字显示全
            formatter: (params) => {
              var newParamsName = ''
              var paramsNameNumber = params.length
              var provideNumber = 4 // 一行显示几个字
              var rowNumber = Math.ceil(paramsNameNumber / provideNumber)
              if (paramsNameNumber > provideNumber) {
                for (var p = 0; p < rowNumber; p++) {
                  var tempStr = ''
                  var start = p * provideNumber
                  var end = start + provideNumber
                  if (p === rowNumber - 1) {
                    tempStr = params.substring(start, paramsNameNumber)
                  } else {
                    tempStr = params.substring(start, end) + '\n'
                  }
                  newParamsName += tempStr
                }
              } else {
                newParamsName = params
              }
              return newParamsName
            }
          }
        }
      ]
      const legendData = []
      let bottom = 20
      const series = data.map((item, index) => {
        legendData.push(item.name)
        xAxis.push({
          data: item.data,
          position: 'bottom', // 将坐标轴定位到底部
          offset: 20 + index * 20, // 设置位置偏移
          axisTick: {
            show: false
          }
        })
        bottom += 20
        return {
          name: item.name,
          data: item.bar,
          stack: 'ad',
          type: 'bar'
        }
      })
      // 最后加一个无data的项，作为表格最底部的线
      xAxis.push({
        position: 'bottom',
        offset: bottom,
        axisTick: {
          show: false
        }
      })
      com.getCompleteOptions(legendData, series, xAxis, bottom + 1)
      com.chart.setOption(com.options)
    },
    /*
    *   导出excel
    *   1. 上传 echarts 的图片
    *   2. 调倒出接口
    */
    async handleExport() {
      this.exportLoading = true
      // 检验
      // try {
      //   const chart = this.$refs.chartCom.$refs.InventoryCom.chart.getDataURL({
      //     type: 'base64', // 导出的格式，可选 png, jpeg
      //     backgroundColor: '#ffffff', // 导出的图片背景色，默认使用 option 里的 backgroundColor
      //     pixelRatio: 1 // 导出的图片分辨率比例，默认为 1。
      //   })
      // } catch (e) {
      //   this.exportLoading = false
      //   this.$notify.error({
      //     title: '错误',
      //     message: '导出前请先展开图表查看'
      //   })
      //   return
      // }
      const res = await this.uploadAllChart()
      // console.log(res)
      if (res.success === false) {
        this.exportLoading = false
        this.$notify.error({
          title: '错误',
          message: '上传错误'
        })
        return
      }
      // 导出
      try {
        const exp = await exportInventoryAnalysisApi({
          ...res.reduce((total, cur) => {
            total[cur.key] = cur.value
            return total
          }, {}),
          wmsInventoryAnalysisDTOList: this.handleSubmitCustomData()
        })
        this.exportLoading = false
        // if (!exp.success) return
        // console.log(exp)
        this.download(exp, '实时库存分析.xls')
      } catch (e) {
        console.log(e)
        this.$notify.error({
          title: '错误',
          message: '导出错误，请重试'
        })
      }
    },
    handleExport1: _debounce(function() {
      const elTable = this.$refs[this.refTableName].$refs[this.refName]
      // 因为element-ui的表格的fixed属性导致多出一个table，会下载重复内容，这里删除掉
      if (elTable.$el.querySelector('.el-table__fixed')) {
        elTable.$el.removeChild(elTable.$el.querySelector('.el-table__fixed'))
      }
      if (elTable.$el.querySelector('.el-table__fixed-right')) {
        elTable.$el.removeChild(elTable.$el.querySelector('.el-table__fixed-right'))
      }
      const wb = XLSX.utils.table_to_book(elTable.$el)
      const wbout = XLSX.write(wb, { bookType: 'xlsx', bookSST: true, type: 'array' })
      try {
        // 导出Excel
        const time = new Date().toLocaleString().replace(/\//g, '').replace(/:/g, '').replace(' ', '')
        saveAs(new Blob([wbout], { type: 'application/octet-stream' }), `订单合同统计分析_${time}.xlsx`)
        this.baseTableKey++ // 更新表格组件，防止操作列消失
      } catch (e) {
        if (typeof console !== 'undefined') console.error(e, wbout)
      }
      return wbout
    }, 800, false),
    download(result, name) {
      const filename = name
      const blob = new Blob([result])
      const url = window.URL.createObjectURL(blob)
      if (window.navigator.msSaveBlob) { // IE
        try {
          window.navigator.msSaveBlob(blob, filename)
        } catch (e) {
          console.log(e)
        }
      } else { // 非IE
        // window.open(url)
        const link = document.createElement('a')
        link.style.display = 'none'
        link.href = url
        link.setAttribute('download', filename)
        document.body.appendChild(link)
        link.click()
      }
      URL.revokeObjectURL(url) // 释放内存
    },
    uploadAllChart() {
      const chartCom = this.$refs.chartCom
      const lib = [
        'InventoryCom',
        'YearChartCom',
        'Ratio',
        'TurnoverRateCom',
        'InventoryAccount',
        'InventoryAccountRatio'
      ]
      const p = lib.map(item => this.uploadChart(chartCom.$refs[item].chart, item))
      return new Promise(resolve => {
        Promise.all(p).then(res => {
          resolve(res)
        }).catch(e => {
          resolve({
            success: false
          })
        })
      })
    },
    async uploadChart(chartRef, name) {
      const lib = {
        InventoryCom: 'inventoryRatioFileId',
        YearChartCom: 'agingRatioFileId',
        Ratio: 'inventoryMonthFileId',
        TurnoverRateCom: 'inventoryRevolveRateFileId',
        InventoryAccount: 'inventoryAgingImageFileId',
        InventoryAccountRatio: 'inventoryAgingRateFileId'
      }
      let chart
      try {
        chart = chartRef.getDataURL({
          type: 'base64', // 导出的格式，可选 png, jpeg
          pixelRatio: 1 // 导出的图片分辨率比例，默认为 1。
        })
      } catch (e) {
        this.$notify.error({
          title: '错误',
          message: '导出前请先展开图表查看'
        })
        throw e
      }

      // 这里对base64串进行操作，去掉url头，并转换为byte
      const bytes = window.atob(chart.split(',')[1])

      const array = []
      for (let i = 0; i < bytes.length; i++) {
        array.push(bytes.charCodeAt(i))
      }
      const blob = new Blob([new Uint8Array(array)], { type: 'image/jpeg' })
      const formData = new FormData()
      formData.append('file', blob, Date.now() + '.jpg')
      const res = await promiseWrapper(uploadApi(
        '/wms/file/upload',
        formData
      ))
      if (!res.success) {
        // this.$notify('上传失败')
        throw res
      }
      return {
        key: lib[name],
        value: res.data.data.id
      }
    },
    /*
    * 用户修改完数据点击确定
    * 1. 提交所有数据给后台，后台生成echarts需要的数据
    * 2. 更新echarts
    */
    handleSubmitCustomData() {
      return this.tableData
    },
    setTable() {
      this.$refs[this.refTableName].sortDialogVisible = true
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.query = INIT_FORM()
      this.searchInit()
    },
    handleModify(item) {
      this.$router.push({
        name: 'AnalysisOrderContractDetail',
        params: {
          id: item.id || 0
        }
      })
    },
    async openDialog() {
      this.dialogLoading = true
      await this.$nextTick()
      // this.dialogVisible = true
      setTimeout(async() => {
        this.dialogTableData = JSON.parse(JSON.stringify(this.tableData))
        this.dialogVisible = true
        await this.$nextTick()
        this.dialogLoading = false
      }, 200)
    },
    // 下拉状态改变
    collapseChange() {
      const { date, ...query } = this.query
      console.log('date: ', date)
      const params = {
        ...query,
        page: {
          ...this.pageInfo
        }
      }
      this.$refs.chartCom.reset()
      this.collapseValue === '1' && this.$refs.chartCom.initCharts(params)
    }
  }
}
</script>

<style lang="scss" scoped>
</style>
