export const generateMonthColumnSet = (propsList, module = 'word') => {
  return propsList.map(prop => {
    return {
      module,
      prop: `${prop.prop}`,
      label: prop.label,
      align: 'center',
      width: module === 'input' ? 280 : '',
      minWidth: module === 'input' ? 280 : '',
      fixed: false,
      sortable: false,
      resizable: true,
      formatter: null,
      headerAlign: 'center'
    }
  })
}

